import req from './request.js'



// 
export let getVodList = async (options,isJia) => {
	let {
		type,
		tag,
		sort,
		page_limit,
		page_start,
		q,
		page
	} = options
	return req.get('/j/search_subjects', {
		type: type || '',
		tag: tag || '',
		sort: sort || '',
		page_limit: page_limit || 6,
		page_start: page_start || 0,
		playable: 'on',
		q:q || '',
		page: page || ''
	},isJia)
}



